import React from 'react';
import PropTypes from 'prop-types';

const PostCard = ({ cover, children }) => (
    <div>
        <div className="head">
            <img src={cover} />
        </div>

        <div className="body">
            {children}
        </div>
    </div>
);

PostCard.propTypes = {
    cover: PropTypes.string.isRequired,
    children: PropTypes.node.isRequired,
};

export default PostCard;
